<template>
  <div id="mircosoft-video">
    <div class="video-container">
      <video
        oncontextmenu="return false;"
        controls
        preload
        autoplay
        :poster="poster"
        controlsList="nodownload"
      >
        <source :src="videoSrc" type="video/mp4">
      </video>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data: function () {
    return {
      poster: 'https://s3-ap-northeast-1.amazonaws.com/microsoft-videos/film_cover.png',
      videoId: this.getParameterByName('id'),
    }
  },
  computed: {
    videoSrc: function () {
      return `https://s3-ap-northeast-1.amazonaws.com/microsoft-videos/${this.videoId}.mp4`
    },
  },
  methods: {
    getParameterByName: function (name, url) {
      if (!url) {
        url = window.location.href
      }
      name = name.replace(/[\[\]]/g, "\\$&")
      const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)")
      const results = regex.exec(url)
      if (!results) {
        return null
      }
      if (!results[2]) {
        return ''
      }

      return decodeURIComponent(results[2].replace(/\+/g, " "))
    },
  },
}
</script>

<style>
video::-internal-media-controls-download-button {
  display:none;
}

video::-webkit-media-controls-enclosure {
  overflow:hidden;
}

video::-webkit-media-controls-panel {
  width: calc(100% + 30px); /* Adjust as needed */
}

.video-container {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 50vw;
  transform: translate(-50%, -50%);
}

.video-container video {
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
}

@media (max-width : 467px) {
  .video-container {
    width: 100%;
    height: unset;
  }
}
</style>



